import React, { useEffect, useState } from "react";
import axios from "axios";
import { sleepInMilliseconds } from "../../helpers/sleepInMilliseconds";
import utilitySlice from "../../slices/utilitySlice";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import MDEditor from "@uiw/react-md-editor";
import ErrorBar from "../Errorbar/ErrorBar";
import { useDispatch } from "react-redux";
import "./blogStyle.css";

const CurrBlog = ({ match, location }) => {
  const [blog, setBlog] = useState({});
  const [com, setCom] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const [reload, setReload] = useState(false);
  const { params } = match;
  const { id } = params;
  const { userInfo } = useSelector((state) => state.userInfo);
  const error = useSelector((state) => state.utilitySlice.showError);
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchData = async () => {
      const res = await axios.request({
        method: "get",
        url: `${process.env.REACT_APP_API_URL_REMOTE}/v1/blogs/blog/${id}`,
      });
      setBlog(res.data.data[0]);
      await sleepInMilliseconds(2500);
      setIsLoading(false);
      return;
    };
    fetchData();
  }, [reload]);

  const likePost = () => {
    const likePostCall = async () => {
      try {
        const res = await axios.request({
          method: "post",
          url: `${process.env.REACT_APP_API_URL_REMOTE}/v1/blogs/like/blog/${id}`,
          data: {
            userDetails: {
              userEmail: userInfo.userEmail,
              userId: userInfo.uuid,
            },
          },
        });
        console.log(res);
        setReload(!reload);
      } catch (err) {
        console.log(err);
        dispatch(
          utilitySlice.actions.setErrorDetails(
            "Author cannot like his/her post."
          )
        );
        dispatch(utilitySlice.actions.showError(true));
      }
    };
    likePostCall();
  };

  const handleComment = (e) => {
    setCom(e.target.value);
  };

  const submitComment = () => {
    const submitPayload = async () => {
      try {
        const res = await axios.request({
          method: "post",
          url: `${process.env.REACT_APP_API_URL_REMOTE}/v1/blogs/comment/blog/${id}`,
          data: {
            messageContent: com,
            userDetails: {
              userName: userInfo.userName,
              userEmail: userInfo.userEmail,
              userId: userInfo.uuid,
            },
            dateAdded: new Date(),
          },
        });
        setIsLoading(true);
        setBlog(res.data.blogContent[0]);
        setIsLoading(false);
      } catch (error) {
        console.log(error);
      }
    };
    submitPayload();
  };

  const {
    author,
    blogTitle,
    comments,
    content,
    dateCreated,
    imgUrl,
    lastModified,
    likes,
    tags,
    views,
    _id,
  } = blog;
  console.log(likes);
  return (
    <main className="">
      {isLoading ? (
        <h1 className="animate-bounce flex justify-center items-center text-6xl">
          Loading
        </h1>
      ) : (
        <main>
          {error ? <ErrorBar /> : <></>}
          <Link to="/blogs" className="myButton">
            Back
          </Link>
          <br />
          <main className="mr-80 ml-80">
            <h1 className="flex justify-center items-center text-4xl underline">
              {blogTitle}
            </h1>
            <br />
            <main className="relative">
              <img className="h-96 w-screen opacity-85" src={imgUrl} />
              <main
                className="absolute flex bottom-0 right-0 bg-white h-12 w-12 mr-4 mb-2 border-2 border-black justify-center items-center text-center"
                style={{ borderRadius: "50%" }}
              >
                <button
                  className={`text-3xl focus:outline-none rounded-full outline-none ${
                    userInfo.isLoggedIn &&
                    likes.length !== 0 &&
                    likes.indexOf(userInfo.uuid) !== -1
                      ? "color-red-400"
                      : ""
                  }`}
                  disabled={!userInfo.isLoggedIn}
                  title={`${
                    !userInfo.isLoggedIn ? "Please login to like a post." : ""
                  }`}
                  onClick={likePost}
                >
                  🤍
                </button>
              </main>
            </main>
            <br />
            <main className="border-r-4 border-l-4">
              <main className="flex text-xl mr-5 ml-5">
                <MDEditor.Markdown source={content} />
              </main>
            </main>
            <form className="border-2 mt-3 mb-3 flex flex-row">
              <input
                placeholder={`${
                  !userInfo.isLoggedIn
                    ? "Log in to post a comment.."
                    : "Add a public comment..."
                }`}
                className="appearance-none bg-transparent border rounded-2 text-gray-700 m-3 py-2 px-2 leading-tight focus:outline-none w-3/4"
                type="text"
                required={true}
                onChange={handleComment}
              />
              <button
                className="myButton1 h-1/2 m-4"
                onClick={submitComment}
                disabled={!userInfo.isLoggedIn}
                title={`${
                  !userInfo.isLoggedIn ? "Please login to post comment." : ""
                }`}
              >
                Post
              </button>
            </form>
            <main className="border-r-2 border-l-2 flex flex-col">
              {comments &&
                comments.length > 0 &&
                comments.map((comment) => {
                  return (
                    <main
                      className="relative flex flex-col"
                      key={comment.messageContent + Date.now()}
                    >
                      <main className="flex flex-row m-1">
                        <main
                          className="justify-center items-center text-center mr-2 ml-2 bg-gray-300"
                          style={{
                            height: "25px",
                            width: "25px",
                            borderRadius: "50%",
                          }}
                        >
                          {comment.userDetails &&
                            comment.userDetails.userName[0]}
                        </main>
                        <main
                          className="text-xl mr-2 ml-2 justify-start"
                          title={
                            comment.userDetails && comment.userDetails.userEmail
                          }
                        >
                          {comment.userDetails && comment.userDetails.userName}
                        </main>
                      </main>
                      <main className="m-3 justify-center items-center">
                        {comment.messageContent && comment.messageContent}
                      </main>
                      <main className="absolute bottom-0 right-0 p-3 italic opacity-60">
                        {comment.dateAdded && comment.dateAdded.slice(0, 10)}
                      </main>
                      <main className="border-b-2 mt-2 mb-2 mr-4 ml-4"></main>
                    </main>
                  );
                })}
            </main>
          </main>
        </main>
      )}
    </main>
  );
};

export default CurrBlog;
