import React, { useState } from "react";
import useLocalStorage from "../../hooks/useLocalStorage";
import { useDispatch, useSelector } from "react-redux";
import utilitySlice from "../../slices/utilitySlice";
import axios from "axios";
import ErrorBar from "../Errorbar/ErrorBar";
import { storage } from "../../firebaseConfig/config";
import {Link} from "react-router-dom";
import MDEditor from '@uiw/react-md-editor';

const defaultImage =
  "https://firebasestorage.googleapis.com/v0/b/kalam-dev-aagg.appspot.com/o/images%2FdefaultImage.jpg?alt=media&token=18963567-e0f3-45d6-b47d-a6b7a9acc481";

const CreateBlog = () => {
  const dispatch = useDispatch();
  const { userInfo } = useSelector((state) => state.userInfo);

  const [title, setTitle] = useLocalStorage("title-uid", "");
  const [tags, setTags] = useLocalStorage("tags-uid");
  const [content, setContent] = useLocalStorage("content-uid", "");
  const [image, setImage] = useState(null);
  const [url, setUrl] = useState(defaultImage);
  const [isImage, setIsImage] = useState(false);

  const uploadPostToBackend = async () => {
    const postDataToBackend = async () => {
      if (!/^[\s\w]*$/gm.test(title)) {
        dispatch(
          utilitySlice.actions.setErrorDetails(
            "Title can only contain alpha-numeric characters."
          )
        );
        dispatch(utilitySlice.actions.showError(true));
        return;
      }

      const tagList = tags.split(",");

      for (let tag in tagList) {
        if (!/^[\w\S]*$/gm.test(tagList[tag])) {
          dispatch(
            utilitySlice.actions.setErrorDetails(
              "A tag can only contain alphabets."
            )
          );
          dispatch(utilitySlice.actions.showError(true));
          return;
        }
      }

      const requestBody = {
        title: title,
        content: content,
        tags: tagList,
        imgUrl: url,
        authorDetails: {
          authorName: userInfo.userName,
          authorEmail: userInfo.userEmail,
          authodId: userInfo.uuid,
        },
      };

      try {
        const response = await axios.post(
          `${process.env.REACT_APP_API_URL_REMOTE}/v1/blogs/`,
          requestBody
        );
        await navigator.clipboard.writeText(
          `http://localhost:3000/#/blogs/${response.data.data._id}`
        );
        dispatch(
          utilitySlice.actions.setErrorDetails(
            "Blog URL has been copied to clipboard."
          )
        );
        dispatch(utilitySlice.actions.showError(true));
        dispatch(utilitySlice.actions.setIsNotError(true));
        localStorage.removeItem("kalam-saved-data-title-uid");
        localStorage.removeItem("kalam-saved-data-tags-uid");
        localStorage.removeItem("kalam-saved-data-content-uid");
        setTags("");
        setTitle("");
        setContent("");
        setUrl("");
        setImage(null);
      } catch (e) {
        dispatch(utilitySlice.actions.setErrorDetails('Error while uploading blog.'));
        dispatch(utilitySlice.actions.showError(true));
      }
    };
    if (isImage) {
      await uploadImgFirebase();
    }
    await postDataToBackend();
  };

  const handleUploadedImage = (e) => {
    if (e.target.files[0]) {
      console.log(e.target.files[0]);
      const fileDetail = e.target.files[0];
      setImage(fileDetail);
      setIsImage(true);
    }
  };

  const uploadImgFirebase = async (event) => {
    const handleImageLikeAPro = async (imgName) => {
      storage
        .ref(`images/${imgName}`)
        .put(image)
        .on(
          "state_change",
          (snapshot) => {},
          (err) => {},
          () => {
            storage
              .ref("images")
              .child(imgName)
              .getDownloadURL()
              .then((data) => {
                const i = data;
                console.log(i);
                setUrl(i);
              });
          }
        );
    };
    const [name, ext] = image.name.split(".");
    await handleImageLikeAPro(`${name}-${Date.now()}-${userInfo.uuid}.${ext}`);
  };

  const error = useSelector((state) => state.utilitySlice.showError);

  return (
    <div className="pt-2">
      {error ? <ErrorBar /> : <></>}
      <div className="flex justify-start m-4">
        <button className="bg-blue-100 px-6 rounded-sm py-2"><Link to="/blogs">Back</Link></button>
      </div>
      <form className="m-4" onSubmit={uploadPostToBackend}>
        <div className="">
          <input
            placeholder="Title"
            className="appearance-none bg-transparent border-b text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none w-2/3"
            type="text"
            required={true}
            onChange={(e) => setTitle(e.target.value)}
            value={title}
          />
          <input
            type="file"
            placeholder="+"
            accept={"image/*"}
            onChange={handleUploadedImage}
            className="text-gray-500"
          />
        </div>
        <div className="mt-4">
          <input
            placeholder="Tags (Comma Separated)"
            className="appearance-none bg-transparent border text-gray-700 mr-3 py-2 px-2 leading-tight focus:outline-none w-2/3"
            type="text"
            required={true}
            onChange={(e) => setTags(e.target.value)}
            value={tags}
          />
        </div>
        <div className="mt-2 ml-1">
          <div className="flex justify-end">
            {content.length === 0 ? 0 : content.split(" ").length} word(s)
          </div>
            <MDEditor
              onChange={setContent}
              value={content}
              height={450}
            />
        </div>
        <div className="flex justify-end mr-4 mt-4">
          <button className="px-6 py-2 bg-blue-100 rounded-sm" type="submit">
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateBlog;
